import React, { useEffect } from 'react';
import Select from 'react-select';
import axios from 'axios'



class SelectSensorsDropDown extends React.Component {
    // let baseUrl = 'http://localhost:8080/api'
    // let baseUrl = 'http://3.18.30.67/api'
    state = {
        selectedOption: {value:'A84041352182B5D2',label:'A84041352182B5D2'},
        options: []

    };

    async componentDidMount() {
        let devicesReadings = await axios.get('http://3.18.30.67/api/all-devices-front');
        console.log("devicesReadings ********", devicesReadings.data.devices)
        let options = devicesReadings?.data?.devices.map((device) => {
            return { value: device._id, label: device._id }
        })
        console.log("options2224", options)
        this.setState({ options: [...this.state.options, ...options] })

    }
    handleChange = (selectedOption) => {
        this.props.selectedSonsor(selectedOption)
        this.setState({ selectedOption }, () =>
            console.log(`Option selected:`, this.state.selectedOption)
            // this.props.selectedSonsor(selectedOption)
        );
    };
    render() {
        const { selectedOption } = this.state;
        console.log("oooopppppttttionsssssssssssss", this.state)
        return (
            <Select
                value={selectedOption}
                onChange={this.handleChange}
                options={this.state.options}
            />
        );
    }
}


export default SelectSensorsDropDown;